import React, { Component } from 'react';
import Utilities from '../../../Utils/Utilities';
import DataVisHeatChart from './DataVisHeatChart';
import ColorHeatMap from './ColorsHeatMap';
import HeatToolTip from './HeatTooltip';
class HeatMapChartContainer extends Component{

    constructor(){
        super();
        this.state={
            isLoading:true,
            isError:false,
            fullchartData: {},
            isTooltipActive:false,
            tooltipData:{}
        }
    }
    
    componentDidMount(){
        setTimeout(() => {
            const storedHeatData=JSON.parse(Utilities.getStorageData("heatdata"));
            if (!storedHeatData){
                this.fetchData();
            }
            else{
                this.setState(prevState=>({
                    fullchartData:storedHeatData,
                    isLoading:false
                }));
            }
        }, 2500);
    }
    onHeatShow=value=>{
        this.setState({isTooltipActive:true,tooltipData:value})
    }
    onHeatHide=()=>{
        this.setState({isTooltipActive:false,tooltipData:{}})
    }
    fetchData(){
        fetch(`https://raw.githubusercontent.com/FreeCodeCamp/ProjectReferenceData/master/global-temperature.json`)
        .then(response=>{
            return response.json();
        })
        .then(result=>{
            Utilities.setStorageData("heatdata",result);
            this.setState({fullchartData:result,isLoading:false});
            
        })
        .catch(Err=>{
            console.log('====================================');
            console.log(`error getting the chart data:${JSON.stringify(Err,null,2)}`);
            console.log('====================================');
                //this.setState({isError:true});
            this.setState(prevState=>({
                isError:true
            }));
        });
    }
    renderToolData=()=>{
        const {fullchartData,tooltipData}= this.state;
        return (
            <div className="heatdataContainer">
                <HeatToolTip data={tooltipData}/>
                
            </div>
        )
    }
    renderGraph=()=>{
        const {fullchartData}= this.state;
        return(<DataVisHeatChart dataChart={fullchartData} activateToolTip={this.onHeatShow} hideToolTip={this.onHeatHide}/>);
    }
    render(){
        const {isError,isLoading,fullchartData,isTooltipActive,tooltipData}= this.state;
        if (isError){
            return (<h3>Lights up the sirens.....Something went wrong</h3>);
        }
        if (isLoading){
            return (<h3>Hold on to your hat...i'm getting the data at lightspeed</h3>);
        }
        if (fullchartData.baseTemperature){
            return(
                <div>
                     <div className="HeatTopText">
                         Monthly Global Land Surface temperature<br/>
                         From 1753 to 2015
                     </div>
                     <div className="heatMap">
                        <ColorHeatMap/>
                     </div>
                     <DataVisHeatChart dataChart={fullchartData} activateToolTip={this.onHeatShow} hideToolTip={this.onHeatHide}/>
                     {isTooltipActive?<div ><HeatToolTip data={tooltipData.data}/></div>:<div></div>}
                     <div className="HeatBottomText">
                        Temperatures are in Celsius and reported as anomalies relative to the Jan 1951-Dec 1980 average.<br/>
                        Estimated Jan 1951-Dec 1980 absolute temperature ℃: 8.66 +/- 0.07
                     </div >
                     
                </div>
            );
        }
    }
}
export default HeatMapChartContainer;


