import React from 'react';

import './Heat.css';
const styles={
    first:{
        width:'35px',
        height:'30px',
        backgroundColor:'#120451',
        color:'#ffffff',
        textAlign:'center'
    },
    second:{
        width:'35px',
        height:'30px',
        backgroundColor:'#3288bd',
        textAlign:'center',
        color:'#ffffff',
    },
    third:{
        width:'35px',
        height:'30px',
        backgroundColor:'#3288bd',
        textAlign:'center',
        color:'#ffffff',
    },
    four:{
        width:'35px',
        height:'30px',
        backgroundColor:'#74f961',
        textAlign:'center',
        color:'#ffffff',
    },
    five:{
        width:'35px',
        height:'30px',
        backgroundColor:'#e5f276',
        textAlign:'center',
        color:'#ffffff',
    },
    six:{
        width:'35px',
        height:'30px',
        backgroundColor:'#eded8b',
        textAlign:'center',
        color:'#ffffff',
    },
    seven:{
        width:'35px',
        height:'30px',
        backgroundColor:'#ffb359',
        textAlign:'center',
        color:'#ffffff',
    },
    eight:{
        width:'35px',
        height:'30px',
        backgroundColor:'#f7883d',
        textAlign:'center',
        color:'#ffffff',
    },
    nine:{
        width:'35px',
        height:'30px',
        backgroundColor:'#f2673c',
        textAlign:'center',
        color:'#ffffff',
    },
    ten:{
        width:'35px',
        height:'30px',
        backgroundColor:'#ff3249',
        textAlign:'center',
        color:'#ffffff',
    },
    eleven:{
        width:'35px',
        height:'30px',
        backgroundColor:'#e80000',
        textAlign:'center',
        color:'#ffffff',
    }

}
const ColorHeatMap=()=>{

    return(
        <div className="containerColors">
            <div style={styles.first}>0</div>
            <div style={styles.second}>2.7</div>
            <div style={styles.third}>3.9</div>
            <div style={styles.four}>5</div>
            <div style={styles.five}>6.1</div>
            <div style={styles.six}>7.2</div>
            <div style={styles.seven}>8.3</div>
            <div style={styles.eight}>9.4</div>
            <div style={styles.nine}>10.5</div>
            <div style={styles.ten}>11.6</div>
            <div style={styles.eleven}>12.7</div>
        </div>
    );
};
export default ColorHeatMap;


