import React, { PureComponent } from 'react';
import {scaleQuantile} from 'd3-scale';
import HeatPoint from './HeatPoint';
class HeatPoints extends PureComponent{

    // findVariance(valueyear,valuemonth){
    //     const{heatData}= this.props;
    //     for (const item in heatData){
            
    //         if (heatData[item].year==valueyear && heatData[item].month==valuemonth){
    //             // console.log('====================================');
    //             // console.log(`findVariance:${item}\nData:${JSON.stringify(heatData[item],null,2)}`);
    //             // console.log('====================================');
    //             return heatData[item];
    //         }
    //     }
    // }
    heatMouseOver=value=>{
        const {heatData,svgDimensions,heatMouseOver,baseTemp}= this.props;
        const {innerWidth,innerHeight } = svgDimensions;

        heatMouseOver(value);
        // const cellID=value.target.id.split('_');
        // const heatItem=this.findVariance(cellID[0],cellID[1]);
        // heatMouseOver(
        //     {
        //         data:{
        //             temp:baseTemp,
        //             variance:heatItem.variance,
        //             year:heatItem.year,
        //             month:heatItem.month
        //         },
        //         position:{
        //             x:(heatItem.year-heatData[0].year)*innerWidth
        //             ,y:(heatItem.month-1)*innerHeight
        //         }
        //     });
    }
    heatMouseLeave=()=>{
       
        this.props.heatMouseLeave();
    }
    render(){
        const {varianceData ,baseTemp,heatData,svgDimensions}= this.props;
        
        const colors = ["#120451", "#3288bd", "#449179", "#74f961", "#e5f276", "#eded8b", "#ffb359", "#f7883d", "#f2673c", "#ff3249", "#e80000"];
        const colorScale= scaleQuantile().domain([varianceData.minVariance+baseTemp,varianceData.maxVariance+baseTemp]).range(colors);
        const { height,width,innerWidth,innerHeight } = svgDimensions;
        const heats=(
            heatData.map(datum=>
                <HeatPoint key={`heat_item_${datum.year}_${datum.month}`} 
                    data={{
                        year:datum.year,
                        baseYear:heatData[0].year,
                        month:datum.month,
                        width:innerWidth,
                        height:innerHeight,
                        variance:datum.variance,
                        baseTemp:baseTemp,
                        fillColor:colorScale(datum.variance+baseTemp)
                    }} 
                    heatDataMouseEnter={this.heatMouseOver} 
                    heatDataMouseLeave={this.heatMouseLeave}/>,
                // <rect
                //     key={`heat_item_${datum.year}_${datum.month}`}
                //     id={`${datum.year}_${datum.month}`}
                //     x={(datum.year-heatData[0].year)*innerWidth}
                //     y={(datum.month-1)*innerHeight}
                //     height={innerHeight}
                //     width={innerWidth}
                //     fill={colorScale(datum.variance+baseTemp)}
                //     onMouseOver={this.heatMouseOver}
                //     onMouseOut={this.heatMouseLeave}/>,
            )
        );
        return(
            <g>
                {heats}
            </g>
        );
    }
}
export default HeatPoints;


