import React, { PureComponent } from 'react';
import {scaleTime,scaleBand} from 'd3-scale';
import HeatAxes from './HeatAxes';
import HeatPoints from './HeatPoints';

class DataVisHeatChart extends PureComponent{
    constructor() {
        super();
        // this.state={
        //     isTooltipActive:false,
        //     tooltipData:{}
        // }
    }
    findMin=()=>{
        const {dataChart}= this.props;
        let min= Number.POSITIVE_INFINITY;
        let high= Number.NEGATIVE_INFINITY;
        let tmp=0;
        for (const item in dataChart.monthlyVariance){
           
            tmp= dataChart.monthlyVariance[item].variance;
            if (tmp<min){
                min=tmp;
            }
            if(tmp>high){
                high=tmp;
            }
        }
       
        return {minVariance:min,maxVariance:high};
    }
    countDups=()=>{
        const {dataChart}= this.props;
        let items=[];
        let current=0;
        for (const item in dataChart.monthlyVariance){
            current= dataChart.monthlyVariance[item].year;
            
           
            let isPresent= items.find(x=>x==current);
            // console.log('====================================');
            // console.log(`Current:${current} ispresent:${isPresent}`);
            // console.log('====================================');
            if (!isPresent){
                items.push(current);
            }
        }
        // console.log('====================================');
        // console.log(`len databy years:${items.length}`);
        // console.log('====================================');
        return items.length;
    }
    onHeatEnter=value=>{
       const {activateToolTip}= this.props;
       activateToolTip(value);
        // this.setState(prevState=>({
        //     isTooltipActive:true,
        //     tooltipData:value
        //   }));
    }
    onHeatLeave=()=>{
        const{hideToolTip}= this.props;
        hideToolTip();
        // this.setState(prevState=>({
        //     isTooltipActive:false,
        //     tooltipData:{}
        //   }));
    }
    render(){
        const {dataChart}= this.props;
       
        const baseTemperature= dataChart.baseTemperature;
        const minDate=new Date(dataChart.monthlyVariance[0].year,0);
        const maxDate= new Date(dataChart.monthlyVariance[dataChart.monthlyVariance.length-1].year,0);
        let databyYears=this.countDups();
        const dataIntervals=this.findMin();
        const listOfMonts=["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        // const margin={
        //     top: 50, right: 20, bottom: 100, left: 60
        // };
        const margin={
            top: 5,
            right: 0,
            bottom: 20,
            left: 100
        };
        let svgDimensions = {
            //width: 1200-margin.left-margin.right,
            width:Math.max(1024,300),
            // height: 550-margin.top-margin.bottom,
            height: 500,
            innerWidth:0,
            innerHeight:0
        };
        svgDimensions.innerHeight=svgDimensions.height/listOfMonts.length;
        svgDimensions.innerWidth=svgDimensions.width/databyYears;

        // console.log('====================================');
        // console.log(`Svg dimensions:${JSON.stringify(svgDimensions,null,2)}`);
        // console.log('====================================');
        
        // console.log('====================================');
        // console.log(`min date:${JSON.stringify(minDate,null,2)} max date:${JSON.stringify(maxDate,null,2)}`);
        // console.log(`min var:${JSON.stringify(dataIntervals.minVariance,null,2)} max var:${JSON.stringify(dataIntervals.maxVariance,null,2)}`);
        // console.log(`gridwidth:${svgDimensions.innerWidth} gridheight:${svgDimensions.innerHeight}`);
        // console.log('====================================');
        //const xScale= scaleTime().domain([minDate,maxDate]).range([margin.left,svgDimensions.width-margin.right]);
        const xScale= scaleTime().domain([minDate,maxDate]).range([0,svgDimensions.width-1]);
        
        // const yScale=scaleBand()
        //     .domain(listOfMonts)
        //     .range([margin.top,svgDimensions.height - margin.bottom]);
        const yScale=scaleBand()
        .domain(listOfMonts)
        .range([0,svgDimensions.height]);
        return(
            <svg width={svgDimensions.width+margin.left+margin.right} height={svgDimensions.height+margin.top+margin.bottom} className="animated bounceInLeft">
              
                <HeatAxes
                        scales={{xScale,yScale}} 
                        margins={margin}
                        svgDimensions={svgDimensions}/>

                <HeatPoints
                    varianceData={dataIntervals} 
                    svgDimensions={svgDimensions} 
                    heatData={dataChart.monthlyVariance} 
                    baseTemp={baseTemperature}
                    heatMouseOver={this.onHeatEnter}
                    heatMouseLeave={this.onHeatLeave}/>
            </svg>
            
        );
    }
}
export default DataVisHeatChart;


